.memory {
  display: flex;
  flex-wrap: wrap;
}
.memory .description {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
.memory .cards {
  width: 600px;
  margin: 0 auto;
}
.memory .card {
  width: 150px;
  height: 150px;
  display: inline-block;
  transition: 0.6s;
  transform-style: preserve-3d;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  cursor: pointer;
}
.memory .card::after {
  content: "";
  background: linear-gradient(to right, #3f0041, #9f3f2f);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotateY(0) perspective(100px);
  transition: 0.4s;
  backface-visibility: hidden;
}
.memory img {
  width: 148px;
  height: 148px;
  position: absolute;
  overflow: hidden;
  object-position: center;
  transform: rotateY(180deg) perspective(100px);
  transition: 0.4s;
  backface-visibility: hidden;
}
.memory .grid-card-show::after {
  transform: rotateY(180deg) perspective(100px);
}
.memory .grid-card-show .grid-img {
  transform: rotateY(0) perspective(100px);
}
.memory .grid-card-finished {
  opacity: 0.5;
}

.daily {
  display: flex;
  flex-direction: column;
}
.daily .description {
  flex: 1 100%;
  text-align: center;
  margin: 10px 0;
}
.daily .items {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.daily .items .ddcontainer:hover {
  transition: background-color 0.5s ease;
  font-weight: bold;
}
.daily .items:focus {
  outline: none;
}
.daily .items .item p {
  letter-spacing: 0.1em;
  cursor: grab;
}
.daily .component_box {
  background-size: contain;
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.daily img {
  height: auto;
  text-align: center;
  width: 60%;
  margin: 0 auto 0;
  justify-content: center;
  display: flex;
  max-height: 66vh;
}
.daily .show {
  margin-bottom: 5%;
}

@media (min-width: 600px) {
  .daily .component_box {
    height: 45vh;
  }
}
.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;
}
.game .status {
  flex: 1 100%;
  text-align: center;
  margin: 10px 0;
}
.game .board {
  display: flex;
  flex-wrap: wrap;
  width: 300px;
  height: 300px;
}
.game .board .square {
  width: 100px;
  height: 100px;
  border: 3px solid #555;
  font-size: 32px;
  text-align: center;
  line-height: 100px;
  cursor: pointer;
  transition: background-color 0.3s;
}
.game .board .square:focus {
  outline: unset;
}
.game .board .square:hover {
  background-color: rgba(240, 230, 220, 0.3);
}
.game .board .square:nth-child(-n+3) {
  border-top: unset;
}
.game .board .square:nth-last-child(-n+3) {
  border-bottom: unset;
}
.game .board .square:nth-child(3n) {
  border-right: unset;
}
.game .board .square:nth-child(3n+1) {
  border-left: unset;
}
.game .result {
  flex: 1 100%;
}

.suitcase {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.suitcase .description {
  width: 75vw;
  margin: 10% auto 0;
  text-align: center;
}

.suitcase .items {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}

.suitcase .items .ddcontainer {
  opacity: 0.5;
}

.suitcase .items .ddcontainer:hover {
  transition: background-color 0.5s ease;
  opacity: 1;
}

.suitcase .items:focus {
  outline: none;
}

.suitcase .bag {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.suitcase .items > * {
  width: 25%;
  margin: auto;
}

.suitcase .items img {
  width: 100%;
  border-radius: 50%;
  cursor: grab;
}

.suitcase .ddcontainerghost img {
  width: 25vw;
}

.suitcase .dragText {
  text-align: center;
}

@media (min-width: 600px) {
  .suitcase .items > * {
    width: 10%;
  }

  .suitcase .ddcontainerghost img {
    width: 10vw;
  }
}
.infoLink {
  position: absolute;
  top: 37%;
  left: 15%;
  cursor: pointer;
  width: 1em;
  height: 1em;
  opacity: 1;
  transition: opacity 0.6s ease, margin-top 0.6s ease;
}
.infoLink .infoText {
  position: absolute;
  top: -20%;
  right: auto;
  margin: 50px;
  opacity: 0;
  overflow: hidden;
  z-index: 11;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  font-size: 1.1rem;
}
.infoLink:hover .infoText {
  padding: 8px;
  opacity: 1;
  transition: opacity 0.5s ease;
}

a {
  color: #fff;
  text-decoration: none;
}

.link-button {
  background-color: transparent;
  text-decoration: none;
  color: var(--color-link);
  border: none;
  cursor: pointer;
  display: inline;
}
.link-button:hover, .link-button:focus {
  color: var(--color-link-highlighted);
}
.link-button .redirect {
  width: 75vw;
  margin: auto;
}

.icon-scroll,
.icon-scroll:before {
  position: absolute;
  left: 50%;
}

.icon-scroll {
  width: 48px;
  height: 84px;
  margin-left: -20px;
  top: 85%;
  margin-top: -35px;
  border: 4px solid var(--color-title);
  border-radius: 25px;
}

.icon-scroll:before {
  content: "";
  width: 10px;
  height: 10px;
  background: var(--color-title);
  margin-left: -4px;
  top: 8px;
  border-radius: 4px;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-name: scroll;
}

@keyframes scroll {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(46px);
  }
}
.puzzle {
  display: flex;
}

.puzzle__solved-board {
  position: relative;
  background-size: cover;
}

.puzzle__solved-board:before {
  content: "";
  background-color: rgba(255, 255, 255, 0.6);
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.puzzle__solved-board,
.puzzle__shuffled-board {
  margin: 20px auto;
  width: 400px;
  list-style-type: none;
  padding: 0;
  font-size: 0;
  border: 1px solid #DDD;
  border-width: 1px 0 0 1px;
}

.puzzle li {
  width: 99px;
  height: 99px;
  position: relative;
  text-align: left;
  display: inline-block;
  border: 1px solid #DDD;
  border-width: 0 1px 1px 0;
}

.puzzle li:empty:hover:before {
  opacity: 1;
}

.puzzle li img {
  width: 100px;
  height: 100px;
  position: absolute;
  cursor: grab;
  transition: transform 200ms ease, box-shadow 200ms ease;
}

.puzzle li img:hover {
  z-index: 2;
  transform: scale(1.1);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8);
}

.smokingPit {
  width: 100%;
  position: relative;
}

.smokingPit img {
  width: 100%;
}

.smoke {
  position: absolute;
  margin-left: -17.5px;
  margin-top: 17.5px;
  bottom: 88%;
  left: 89.5%;
}

.smoke span {
  display: block;
  position: absolute;
  bottom: -35px;
  margin-left: -20px;
  height: 0px;
  width: 0px;
  border: 35px solid #4b4b4b;
  border-radius: 35px;
  left: -14px;
  opacity: 0;
  transform: scale(0.2);
}

@keyframes smoke {
  0% {
    transform: scale(0.2) translate(0, 0);
  }
  1% {
    opacity: 1;
    transform: scale(0.2) translate(-20px, -20px);
  }
  100% {
    opacity: 0;
    transform: scale(1) translate(-200px, -50px);
  }
}
.smoke .s0 {
  animation: smoke 10s 0s infinite;
}

.smoke .s1 {
  animation: smoke 10s 1s infinite;
}

.smoke .s2 {
  animation: smoke 10s 2s infinite;
}

.smoke .s3 {
  animation: smoke 10s 3s infinite;
}

.smoke .s4 {
  animation: smoke 10s 4s infinite;
}

.smoke .s5 {
  animation: smoke 10s 5s infinite;
}

.smoke .s6 {
  animation: smoke 10s 6s infinite;
}

.smoke .s7 {
  animation: smoke 10s 7s infinite;
}

.smoke .s8 {
  animation: smoke 10s 8s infinite;
}

.smoke .s9 {
  animation: smoke 10s 9s infinite;
}

.chapter_picture {
  max-width: 100%;
  display: block;
  filter: saturate(0.8);
  opacity: 0.9;
  transition: all 0.5s;
}
.chapter_picture:hover {
  transition: all 0.5s;
  filter: brightness(110%);
  transform: scale(1.1);
}
.chapter_picture:hover .overlay {
  opacity: 1;
}

img {
  height: auto;
  width: 100%;
}

.overlay {
  position: absolute;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  color: white;
  padding: 20px;
  text-align: center;
}

.primary {
  width: 66%;
}

.second {
  width: 50%;
}

.third {
  width: 33%;
  padding: 5px;
}

.avatar img {
  border-radius: 50%;
}

.bg {
  opacity: 0.3;
}

video {
  width: 100%;
  height: auto;
  box-shadow: 5px 5px 5px var(--color-text);
}

.chapter_video {
  position: relative;
}

.fourth {
  flex: 1 25%;
  padding: 5px;
}

.theater {
  flex: 1 auto;
  margin: 0 17%;
  order: 99;
}

.box {
  width: 80%;
}
.box.avatar {
  flex: 1 25%;
  padding: 5px;
}
.box.avatar:hover p {
  background-color: var(--color-title);
}
.box.avatar.second {
  flex: 1 50%;
}
.box.avatar p {
  display: flex;
  align-items: center;
  overflow: hidden;
  border-radius: 50%;
  height: 200px;
  margin: 8px;
  color: #ffffff;
  background-color: #673ab7;
  text-shadow: none;
  justify-content: center;
}
.box p {
  text-shadow: 2px 2px lightgrey;
}

.column {
  flex-flow: column nowrap;
}

p {
  text-align: center;
}

.reveal span {
  position: relative;
  opacity: 0;
  animation: move-text 0.1s forwards;
}

@keyframes move-text {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
footer {
  text-align: center;
  background-color: tomato;
  color: white;
}

.navigation {
  right: 2%;
  position: absolute;
  top: 50%;
  z-index: 6;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  list-style-type: none;
}

.navigation--item {
  cursor: pointer;
  border-radius: 9999px;
  height: 20px;
  width: 20px;
  background-color: transparent;
  border: 1px solid rgba(0, 0, 0, 0.3);
  margin: 8px 0;
  position: relative;
  display: block;
}

.navigation--item a {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  box-shadow: inset 0 0 0 2px white;
  cursor: pointer;
  height: 100%;
  left: 0;
  outline: none;
  text-indent: -999em;
  top: 0;
  transition: background 0.3s ease;
  width: 100%;
  overflow: hidden;
  position: absolute;
}

.navigation--item a:focus {
  outline: none;
}

.navigation--item a:hover,
.navigation--item a:focus {
  background-color: rgba(0, 0, 0, 0.2);
}

.navigation--item a::after {
  content: "";
  position: absolute;
  bottom: 0;
  height: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  transition: height 0.3s ease;
}

.navigation--item a.active::after {
  height: 100%;
}

.title {
  color: var(--color-title);
  text-align: center;
}
.title h1 {
  line-height: 1.2;
  font-size: 3em;
  font-family: LinolschriftHeavy, arial, sans-serif;
  font-weight: normal;
}

.parallax {
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 300px;
}
.parallax__group {
  position: relative;
  height: 100vh;
  transform-style: preserve-3d;
}
.parallax__header {
  z-index: 99;
}
.parallax__layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: row wrap;
}
.parallax__layer--fore {
  transform: translateZ(90px) scale(0.7);
  z-index: 1;
  align-content: baseline;
}
.parallax__layer--base {
  transform: translateZ(0);
  z-index: 4;
  background: 50% 50%/cover;
}
.parallax__layer--back {
  transform: translateZ(-300px) scale(2);
  z-index: 3;
  align-content: baseline;
}
.parallax__layer--deep {
  transform: translateZ(-600px) scale(3);
  z-index: 2;
  height: 50vh;
}

.back {
  z-index: -1;
}

.active {
  z-index: 25;
}

.fade-in-scale {
  animation: fade-in-scale-down 5s ease-out both;
}

@keyframes fade-in-scale-down {
  0% {
    transform: scale(1.25) translateY(-15px);
    transform-origin: 50% 16%;
    filter: blur(5px);
  }
  100% {
    transform: scale(1) translateY(0);
    transform-origin: top;
    filter: blur(0);
  }
}